.section {

  @include grid();
  &__picture {
    width: 280px;
    height: 280px;
    border-radius: 4px;
  }

  &__title {
    font-family: "Space Grotesk", sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0;
    color: #292929;
  }

  &__paragraph {
    font-family: "Space Mono", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;
    color: #292929;
  }

  &__price {
    font-family: "Space Grotesk",sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0px;
    color: #292929;
  }

  &__article {
    
  }
}
